const validateSuggestClicked = (payload) => (typeof payload.enteredPhrase === "string") &&
    (typeof payload.suggestions === "object") &&
    payload.suggestions.every(({
        type,
        value,
        id
    }) => 
        (typeof type === "string") && (typeof value === "string") && (typeof id === "number")
    ) &&
    (typeof payload.positionOfClickedItem === "number") &&
    (typeof payload.timestamp === "string") &&
    (typeof payload.beaconId === "string") &&
    (typeof payload.app === "string") &&
    (typeof payload.siteId === "number") &&
    (typeof payload.siteDomain === "string") &&
    (typeof payload.suggestionId === "string") &&
    (typeof payload.pvUniqueId === "string");

export default validateSuggestClicked;
