const validateSearchImpression = (payload) => (typeof payload.searchPhrase === "string") &&
    (typeof payload.results === "object") &&
    payload.results.every(({
        id,
        title,
        position,
        thumbnail
    }) => 
        (typeof id === "string") && (typeof title === "string") && (typeof position === "number") && (typeof thumbnail === "boolean")
    ) &&
    ((typeof payload.page === "number") || (typeof payload.offset === "number")) &&
    (typeof payload.limit === "number") &&
    (typeof payload.sortOrder === "string") &&
    (typeof payload.timestamp === "string") &&
    (typeof payload.beaconId === "string") &&
    (typeof payload.app === "string") &&
    (typeof payload.siteId === "number") &&
    (typeof payload.siteDomain === "string") &&
    (typeof payload.searchId === "string") &&
    (typeof payload.pvUniqueId === "string");

export default validateSearchImpression;
