const validateSearchClicked = (payload) => (typeof payload.searchPhrase === "string") &&
    (typeof payload.clicked === "object") &&
    (typeof payload.clicked.type === "string") &&
    (typeof payload.clicked.id === "string") &&
    (typeof payload.clicked.title === "string") &&
    (typeof payload.clicked.position === "number") &&
    (typeof payload.clicked.thumbnail === "boolean") &&
    (typeof payload.timestamp === "string") &&
    (typeof payload.beaconId === "string") &&
    (typeof payload.app === "string") &&
    (typeof payload.siteId === "number") &&
    (typeof payload.siteDomain === "string") &&
    (typeof payload.searchId === "string") &&
    (typeof payload.pvUniqueId === "string");

export default validateSearchClicked;
